export class Grid {
  wordSelectMode = false;
  selectedItems = [];
  firstSelectedItem;
  gridArea = null;
  words = [];
  foundWords = [];
  tbl;
  hint = document.querySelector(".hint");
  success = document.querySelector(".success");

  getCellsInRange(firstLetter, currentLetter) {
    let cellsInRange = [];

    // Horizontal
    if (firstLetter.y === currentLetter.y) {
      if (firstLetter.x === currentLetter.x) return cellsInRange;
      if (firstLetter.x > currentLetter.x)
        [currentLetter, firstLetter] = [firstLetter, currentLetter];
      for (let i = firstLetter.x; i <= currentLetter.x; i++) {
        cellsInRange.push(
          this.gridArea.querySelector(
            `td[data-x="${i}"][data-y="${currentLetter.y}"]`
          )
        );
      }
      // Vertical
    } else if (firstLetter.x === currentLetter.x) {
      if (firstLetter.y === currentLetter.y) return cellsInRange;
      if (firstLetter.y > currentLetter.y)
        [currentLetter, firstLetter] = [firstLetter, currentLetter];
      for (let i = firstLetter.y; i <= currentLetter.y; i++) {
        cellsInRange.push(
          this.gridArea.querySelector(
            `td[data-x="${currentLetter.x}"][data-y="${i}"]`
          )
        );
      }
      // Diagonal: both x and y increase or decrease together
    } else if (
      currentLetter.x - firstLetter.x ===
      currentLetter.y - firstLetter.y
    ) {
      if (firstLetter.x === currentLetter.x) return cellsInRange;
      if (firstLetter.y === currentLetter.y) return cellsInRange;
      if (firstLetter.y > currentLetter.y)
        [currentLetter, firstLetter] = [firstLetter, currentLetter];
      for (let i = 0; i <= currentLetter.y - firstLetter.y; i++) {
        cellsInRange.push(
          this.gridArea.querySelector(
            `td[data-x="${firstLetter.x + i}"][data-y="${firstLetter.y + i}"]`
          )
        );
      }
      // Inverse diagonal: x increases and y decreases or the opposite
    } else if (
      currentLetter.x - firstLetter.x ===
      firstLetter.y - currentLetter.y
    ) {
      if (firstLetter.x === currentLetter.x) return cellsInRange;
      if (firstLetter.y === currentLetter.y) return cellsInRange;
      if (firstLetter.y < currentLetter.y)
        [currentLetter, firstLetter] = [firstLetter, currentLetter];
      for (let i = 0; i <= currentLetter.x - firstLetter.x; i++) {
        cellsInRange.push(
          this.gridArea.querySelector(
            `td[data-x="${firstLetter.x + i}"][data-y="${firstLetter.y - i}"]`
          )
        );
      }
    } else {
      cellsInRange.push(
        this.gridArea.querySelector(
          `td[data-x="${firstLetter.x}"][data-y="${firstLetter.y}"]`
        )
      );
    }

    return cellsInRange;
  }

  // Event Handlers
  downHandler(e) {
    // if (this.clickId !== 0) return;
    e.preventDefault();

    if (!e.target.closest("td")) return;
    if (e.target.classList.contains("selected-correct")) return;

    this.success.classList.add("hidden");
    // this.wordSelectMode = true;

    const cell = e.target;
    cell.classList.add("selected");
    const x = +cell.dataset.x;
    const y = +cell.dataset.y;

    this.firstSelectedItem = { x, y };

    this.tbl.removeEventListener("click", this.downHandlerBoundThis);
    this.tbl.addEventListener("click", this.moveHandlerBoundThis);
  }

  moveHandler(e) {
    e.preventDefault();

    // if (!this.wordSelectMode) return;

    // if (this.clickId !== 1) return;
    if (!e.target.closest("td")) return;
    if (e.target.classList.contains("selected")) return;

    const cell = e.target;
    const x = +cell.dataset.x;
    const y = +cell.dataset.y;
    this.selectedItems.forEach((cell) => cell.classList.remove("selected"));
    this.selectedItems = this.getCellsInRange(this.firstSelectedItem, {
      x,
      y,
    });
    this.selectedItems.forEach((cell) => cell.classList.add("selected"));

    this.hint.classList.remove("hidden");

    this.tbl.removeEventListener("click", this.moveHandlerBoundThis);
    this.tbl.addEventListener("click", this.upHandlerBoundThis);
  }

  upHandler(e) {
    e.preventDefault();

    // this.wordSelectMode = false;

    const selectedWord = this.selectedItems.reduce(
      (word, cell) => (word += cell.dataset.letter),
      ""
    );
    const reversedSelectedWord = selectedWord.split("").reverse().join("");
    if (this.words.indexOf(selectedWord) !== -1) {
      this.foundWords.push(selectedWord);
      this.selectedItems.forEach((cell) =>
        cell.classList.add("selected-correct")
      );
      this.selectedItems = [];
      this.success.classList.remove("hidden");
    } else if (this.words.indexOf(reversedSelectedWord) !== -1) {
      this.foundWords.push(reversedSelectedWord);
      this.selectedItems.forEach((cell) =>
        cell.classList.add("selected-correct")
      );
      this.selectedItems = [];
      this.success.classList.remove("hidden");
    } else {
      this.selectedItems.forEach((cell) => cell.classList.remove("selected"));
    }

    this.hint.classList.add("hidden");

    this.tbl.removeEventListener("click", this.upHandlerBoundThis);
    this.tbl.addEventListener("click", this.downHandlerBoundThis);
  }

  downHandlerBoundThis = this.downHandler.bind(this);
  moveHandlerBoundThis = this.moveHandler.bind(this);
  upHandlerBoundThis = this.upHandler.bind(this);

  renderGrid(gridSize, wordSearchArray) {
    // Render grid
    this.gridArea = document.querySelector(".grid-area");
    if (this.gridArea.innerHTML !== "") this.gridArea.innerHTML = "";
    this.tbl = document.createElement("table");
    const tblBody = document.createElement("tbody");
    let index = 0;
    for (let i = 0; i < gridSize; i++) {
      const row = document.createElement("tr");
      for (let j = 0; j < gridSize; j++) {
        const cell = document.createElement("td");
        const letter = wordSearchArray[index++];
        const cellText = document.createTextNode(letter);
        cell.appendChild(cellText);
        cell.setAttribute("data-x", j);
        cell.setAttribute("data-y", i);
        cell.setAttribute("data-letter", letter);
        row.appendChild(cell);
      }
      tblBody.appendChild(row);
    }
    this.tbl.appendChild(tblBody);
    this.gridArea.appendChild(this.tbl);

    // Attach event handlers

    this.tbl.addEventListener("click", this.downHandlerBoundThis);
    // tbl.addEventListener("mousedown", downHandlerBoundThis);
    // tbl.addEventListener('touchstart', downHandlerBoundThis, false)

    // tbl.addEventListener("click", moveHandlerBoundThis);
    // tbl.addEventListener("mousemove", moveHandlerBoundThis);
    // tbl.addEventListener('touchmove', this.moveHandler.bind(this), false)

    // tbl.addEventListener("click", upHandlerBoundThis);
    // tbl.addEventListener("mouseup", upHandlerBoundThis);
    // tbl.addEventListener('touchend', this.upHandler.bind(this), false)
  }
}
