import { Grid } from "./grid";

const submitWordBtn = document.querySelector(".submit-word");

submitWordBtn.addEventListener('click', async function(){
  const grid = new Grid();
  const commaSeparatedWords = document.querySelector(".words-input").value
  const gridSize = document.querySelector(".grid-size-input").value

  if (!commaSeparatedWords || !gridSize) {
    const gridArea = document.querySelector(".grid-area");
    gridArea.innerHTML = `<h1 class="error">Please enter comma-separated list of words and a grid size</h1>`;
    return;
  }

  const wordSearchArray = await fetchGridInfo(gridSize, commaSeparatedWords);
  grid.words = commaSeparatedWords.toUpperCase().split(",");
  grid.renderGrid(gridSize, wordSearchArray);
});

async function fetchGridInfo(gridSize, commaSeparatedWords){
  const response = await fetch(`http://localhost:8080/wordgrid?gridSize=${gridSize}&wordList=${commaSeparatedWords}`);
  const result = await response.text();
  const resultArr = result.split(" ").filter(el => el != "\r\n" && el != "");
  return resultArr;
}